<template>
  <div id="chats">
    <img src="../../assets/img/head.jpg" alt="" :class="selectRole">
    <p :class="selectRole"><span class="arrow-l" :class="selectRole"></span>nihaonihao </p>
  </div>
</template>

<script>
  export default {
    name: 'chats',
    props: ['type'],
    data () {
      return {

      }
    },
    computed: {
      selectRole(){
        return {
          user: this.type == 'user',
          admin: this.type == 'admin'
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #chats{
    min-height: 40px;
    margin: 16px 0;
  }
  img{
    width: 40px;
    height: 40px;
    border-radius: 50%;
  }
  img.user{
    float: left;
    margin-left: 10px;
  }
  img.admin{
    float: right;
    margin-right: 10px;
  }
  p{
    position: relative;
    top: 6px;
    box-sizing: border-box;
    padding: 4px 6px;
    min-height: 30px;
    max-width: 260px;
    border: 1px solid #00ca79;
    background-color: #00ca79;
    color: #fff;
    border-radius: 5px;
    word-wrap: break-word;
  }
  p.user{
    float: left;
    margin-left: 10px;
  }
  p.admin{
    float: right;
    right: 10px;
  }
  .arrow-l{
    position: absolute;
    top: 6px;
    display: inline-block;
    width: 0;
    height: 0;
    border: 6px solid transparent;

  }
  .arrow-l.user{
    left: -12px;
    border-right-color: #00ca79;
  }
  .arrow-l.admin{
    right: -12px;
    border-left-color: #00ca79;
  }
</style>



