<template>
  <div class="content">
    <div id="phone">
      <div class="phone-tabs">
        <a href="javascript:;" @click="toggleL">
          <i class="iconfont icon-contacts"></i>
          联系人
        </a>
        <a href="javascript:;" @click="toggleR">
          <i class="iconfont icon-news"></i>
          消息
        </a>
        <div class="bar">
          <span :class="{ 'toggle-right': toggleRight }"></span>
        </div>
      </div>
      <div id="contact-list" :class="{ 'toggle-left': toggleLeft }">
        <contact></contact>
        <contact></contact>
        <contact></contact>
        <contact></contact>
        <contact></contact>
        <news-paging class="news-paging"></news-paging>
      </div>
      <div id="news" :class="{ 'toggle-left': toggleLeft }">
        <contact>
          <span slot="badge" class="badge">20</span>
        </contact>
        <contact>
          <span slot="badge" class="badge">20</span>
        </contact>
        <contact>
          <span slot="badge" class="badge">20</span>
        </contact>
        <contact>
          <span slot="badge" class="badge">20</span>
        </contact>
        <contact>
          <span slot="badge" class="badge">20</span>
        </contact>
        <news-paging class="news-paging"></news-paging>
      </div>
    </div>
    <div id="chat-box">
      <div class="head-bar">
        <span>小小123</span>
        <a href="javascript:;">其他联系方式</a>
      </div>
      <div id="news-bar">
        <chats :type="'user'"></chats>
        <chats :type="'admin'"></chats>
        <chats :type="'user'"></chats>
        <chats :type="'user'"></chats>
        <chats :type="'user'"></chats>
        <chats :type="'user'"></chats>
        <chats :type="'user'"></chats>
        <chats :type="'admin'"></chats>
        <chats :type="'admin'"></chats>
      </div>
      <div id="input-bar">
        <input type="text">
        <a href="javascript:;">发送</a>
      </div>
    </div>
  </div>
</template>

<script>
  import contact from './contact'
  import newsPaging from './news-paging'
  import chats from './chats'
  export default {
    name: 'newsChat',
    components: {
      contact,
      newsPaging,
      chats
    },
    data () {
      return {
        toggleLeft: false,
        toggleRight: false
      }
    },
    methods: {
      toggleR(){
        this.toggleLeft = true;
        this.toggleRight = true;
      },
      toggleL(){
        this.toggleLeft = false;
        this.toggleRight = false;
      }
    }

  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .toggle-left{
    transform: translateX(-100%);
  }
  .toggle-right{
    transform: translateX(100%);
  }
  #phone{
    float: left;
    width: 300px;
    height: 410px;
    border: 1px solid #999;
    margin-left: 50px;
    overflow: hidden;
    position: relative;
  }
  .phone-tabs{
    width: 100%;
    font-size: 0px;
  }
  .phone-tabs a{
    display: inline-block;
    box-sizing: border-box;
    width: 50%;
    height: 46px;
    line-height: 46px;
    text-indent: 45px;
    background-color: #00ca79;
    font-size: 12px;
    color: #fff;
    font-weight: bold;
  }
  .phone-tabs a:first-child{
    border-right: 1px solid #fff;
  }
  .phone-tabs .iconfont{
    font-weight: normal;
    font-size: 24px;
  }
  .phone-tabs .bar{
    width: 100%;
    height: 4px;
    background-color: #f3f3f3;
  }
  .phone-tabs .bar span{
    display: inline-block;
    width: 50%;
    height: 4px;
    background-color: #ffa608;
    transition: .5s;
  }
  #contact-list{
    height: 360px;
    position: relative;
    transition: .5s;
  }
  .news-paging{
    position: absolute;
    left: 0;
    bottom: 0;
  }
  #news{
    width: 100%;
    height: 360px;
    position: absolute;
    left: 300px;
    top: 50px;
    transition: .5s;
  }
  .badge{
    display: inline-block;
    width: 30px;
    height: 20px;
    line-height: 20px;
    text-align: center;
    background-color: #00ca79;
    border-radius: 4px;
    color: #fff;
    font-weight: bold;
    margin-left: 140px;
  }
  #chat-box{
    overflow: hidden;
    width: 586px;
    height: 410px;
    border: 1px solid #999999;
    position: relative;
    left: 35px;
  }
  .head-bar{
    height: 46px;
    line-height: 46px;
    background-color: #00ca79;
    color: #fff;
    border-bottom: 4px solid #f3f3f3;
  }
  .head-bar span{
    margin-left: 15px;
  }
  .head-bar a{
    float: right;
    margin-right: 15px;
    padding: 4px 6px;
    color: #fff;
    line-height: 38px;
  }
  .head-bar a:hover{
    font-weight: bold;
  }
  #news-bar{
    width: 100%;
    height: 320px;
    overflow-y: auto;
    overflow-x: hidden;
  }
  #input-bar{
    width: 100%;
    height: 40px;
    line-height: 40px;
    background-color: #00ca79;
  }
  #input-bar input{
    width: 480px;
    height: 26px;
    outline: none;
    margin-left: 20px;
  }
  #input-bar a{
    padding: 6px 20px;
    color: #fff;
  }
  #input-bar a:hover{
    font-weight: bold;
  }
</style>



